import React from 'react';
import { Row, Col } from 'antd';
import { connect } from "react-redux";
import { formUploadItem } from "./form-item-config";
import Avatar from "../components/Avatar/Avatar";
import Select from "../components/Select/Select";
import "./FormContent.css"
// import axios from "axios";

class FormContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hospital: localStorage.getItem('hospital') || "angiang"
    }
  }

  componentDidMount = () => {

    // axios.get('http://localhost:6700/st/hospitals/angiang/content.json')
    //   .then(function (response) {
    //     // handle success
    //     const data = response.data
    //     // console.log(JSON.stringify(data).replace(/(?:\\[rn])+/g, ""))
    //     const string = JSON.stringify(data).replace(/(?:\\[rn])+/g, "");
    //     const object = JSON.parse(JSON.parse(string))
    //     console.log(object);
    //   })
    //   .catch(function (error) {
    //     // handle error
    //     console.log(error);
    //   })
    //   .then(function () {
    //     // always executed
    //   });
  }

  changeHospital = (value) => {
    localStorage.setItem('hospital', value)
    window.location.reload();
  }

  // onFinish = values => {
  //   console.log('Success:', values);
  // };

  // onFinishFailed = errorInfo => {
  //   console.log('Failed:', errorInfo);
  // };



  showAvatar = () => {
    const result = formUploadItem.map((item, index) => {
      return <Col xs={24} sm={12} md={8} lg={4} key={index}>
        <Avatar label={item.label} name={item.name} hospital={this.state.hospital} />
      </Col >
    })

    return result
  }

  render() {

    return (
      <div>
        <Select changeHospital={this.changeHospital} default={this.state.hospital} />
        <br />
        <br />

        <Row justify="space-around">
          {this.showAvatar()}

        </Row>

      </div >

    );
  }
}


// const mapStateToProps = state => {

