import React from 'react';
import { Upload, message } from 'antd';
import { LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import { currentEnv } from "../../configs";

function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}

function beforeUpload(file) {
  // const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/svg+xml';
  const isJpgOrPng = file.type === 'image/png';
  if (!isJpgOrPng) {
    // message.error('You can only upload JPG/PNG/SVG file!');
    message.error('You can only upload PNG file!');
  }
  const isLt2M = file.size / 1024 / 1024 < 5;
  if (!isLt2M) {
    message.error('Image must smaller than 5MB!');
  }
  return isJpgOrPng && isLt2M;
}

class Avatar extends React.Component {
  state = {
    loading: false,
    imageUrl: `${currentEnv.DOMAIN_URL}/st/hospitals/${this.props.hospital}/${this.props.name}.png`
  };

  handleChange = info => {
    if (info.file.status === 'uploading') {
      this.setState({ loading: true });
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      getBase64(info.file.originFileObj, imageUrl => {
        this.setState({
          imageUrl,
          loading: false,
        })
      }
      );

    }
  };

  UNSAFE_componentWillReceiveProps = (newProps) => {
    this.setState({ imageUrl: `${currentEnv.DOMAIN_URL}/st/hospitals/${newProps.hospital}/${newProps.name}.png` });

  }

  render() {
    const uploadButton = (
      <div>
        {this.state.loading ? <LoadingOutlined /> : <PlusOutlined />}
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    const { imageUrl } = this.state;

    return (
      <div className="upload">
        <Upload
          name="image"
          listType="picture-card"
          className="avatar-uploader"
          showUploadList={false}
          action={`${currentEnv.DOMAIN_URL}/upload`}
          headers={{ hospital: this.props.hospital, name: this.props.name }}
          beforeUpload={beforeUpload}
          onChange={this.handleChange}
        >
          {imageUrl ? <img src={imageUrl} alt="" style={{ height: '86px' }} /> : uploadButton}
        </Upload>
        <p>{this.props.label}</p><br />
      </div>

    );
  }
}

export default Avatar;